import store from "@/store"
import config from './public/config.js'

function api(obj) {
	return new Promise((resolve, reject) => {
		// const HOST = 'http://192.168.1.126:9999/xujun/';
		//const HOST = 'https://test.net/';
		// const HOST = 'https://share.qingchuangweb.com' // 
		// const HOST = "https://test.qingchuangweb.com:8443"; //' // 
		//const HOST = "http://192.168.1.52:8891/dev-api"; // 
		const HOST = config.HOST; // 
		var method = obj.method || "GET";
		var url = HOST + obj.url || "";
		var data = obj.data || {};
		var header = {
			'Content-Type': obj.contentType || 'application/json',
			'Authorization-xcb': obj.Authorization ? ('Bearer ' + obj.Authorization) : '' // obj.Authentication || '' // ('Bearer ' +  obj.Authentication) || ''
		};
		var success = obj.success; // 
		var fail = obj.fail; //

		var noLoading = obj.noLoading;

		// if (obj.showLoading) {
		// 	uni.showLoading({
		// 		title: obj.showLoading || '',
		// 	})
		// }

		if (!uni.getStorageSync('isLogin') && url.indexOf('login') == -1 && url.indexOf('list-industry') == -1) {
			console.log('url', url)

			uni.showToast({
				title: ',!',
				icon: "none"
			})


			setTimeout(() => {
				uni.reLaunch({
					url: '/pages/login/login_app2'
				})
			}, 500)

			return
		}

		if (!noLoading) {
			console.log(store)
			store.commit('SET_LOADING_OPEN')
		}

		// setTimeout( ()=> {
		// 		if (!noLoading) {
		// 			console.log(store)
		// 			store.commit('SET_LOADING_CLOSE')
		// 		}
		// },3000)

		const app = getApp()
		// 
		uni.onNetworkStatusChange(function(res) {
			console.log('ing', res)
			let isConnected = res.isConnected
			let networkType = res.networkType
			let networkTypeArr = ['4g', '3g', 'wifi']
			// 
			if (!isConnected) {
				uni.showToast({
					title: '',
					icon: "none"
				})
			} else {
				// 
				if (!networkTypeArr.includes(networkType)) {
					uni.showToast({
						title: '',
						icon: "none"
					})
				}
			}
		})


		uni.request({
			url: url,
			data: data,
			method: method,
			header: header,
			success: ((res) => {
				console.log('request-res', res)
				if (res.statusCode === 403 || res.statusCode === 401) {
					store.commit('SET_LOADING_CLOSE')
					// 
					uni.reLaunch({
						url: '/pages/login/login_app2'
					})
				} else if (res.statusCode === 200) {
					store.commit('SET_LOADING_CLOSE')

					/* -start */
					if (res.data.code == 401) { //

						uni.showToast({
							title: ',!',
							icon: "none"
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '/pages/login/login_app2'
							})
						}, 500)

					}
					/* -end */


					resolve(res.data)
				}
				// if (obj.showLoading) {
				// 	uni.hideLoading()
				// }
				// store.commit('SET_LOADING_CLOSE')
			}),
			fail: ((err) => {
				// if (obj.showLoading) {
				// 	uni.hideLoading()
				// }
				uni.showToast({
					title: '!',
					icon: "none"
				})
				// uni.reLaunch({
				// 	url: '/pages/common/netWork/no-netWork'
				// })

				store.commit('SET_LOADING_CLOSE')
				reject(err)
			})
		})
	})
}
export default api
