//  yy-mm-dd hh:mm:ss
let toDate = function(number) {
	var n = number - 0;
	var date = new Date(n);
	var Y = date.getFullYear() + '-';
	var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
	var D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' ';
	var H = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
	var MI = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
	var S = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds());
	return (Y + M + D + H + MI + S)
};
// 
let getNowFormatDate = function () {
        var date = new Date();
        var seperator1 = "-";
        var year = date.getFullYear();
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
        }
        var currentdate = year + seperator1 + month + seperator1 + strDate;
        return currentdate;
    }

//  yy-mm-dd
let toDateNoTime = function(number) {
	var n = number - 0;
	var date = new Date(n);
	var Y = date.getFullYear() + '-';
	var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
	var D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate());
	return (Y + M + D)
};


// 
let isAuthorizationTrue = function(backDelta) {
	uni.getSetting({
		success: res => {
			if (!res.authSetting["scope.userInfo"]) {
				uni.navigateTo({
					url: `/pages/login/login?backDelta=${backDelta}`,
				})
			}
		}
	})
};

// 
let isLogin = function(backDelta) {
	let memberId = uni.getStorageSync('memberId') || ''
	if (!memberId) {
		uni.navigateTo({
			url: `/pages/login/login?backDelta=${backDelta}`,
		})
		return
	}
};


// 
let isInteger = function(obj) {
	if (Math.round(obj) === obj) {
		return obj.toFixed(1);
	} else {
		return obj
	}
};

/***
 * 
 * 
 */
let getTouchData = (endX, endY, startX, startY) => {
	let turn = "";
	if (endX - startX > 50 && Math.abs(endY - startY) < 50) { //
		turn = "right";
	} else if (endX - startX < -50 && Math.abs(endY - startY) < 50) { //
		turn = "left";
	}
	return turn;
};

// 
let formatTel = function(tel) {
	return tel.substring(0, 3) + '****' + tel.substring(7, 11);
};
let isPhone = function (tel) {
    var reg =/^0?1[3|4|5|6|7|8][0-9]\d{8}$/;
    return reg.test(tel);
};
