let numberZzs = function(val) {
	console.log('numberZzs',val)
	if(!(/(^[1-9]\d*$)/.test(val))) {
		return 0
	} else {
		return val
	}
}

// 
let getDistance = function(val1,val2){
		
		let lat1 = val1.lat
		let lng1 = val1.lng
		let lat2 = val2.lat
		let lng2 = val2.lng
		
		
		
		
		var La1 = lat1 * Math.PI / 180.0;
		var La2 = lat2 * Math.PI / 180.0;
		var La3 = La1 - La2;
		var Lb3 = lng1 * Math.PI / 180.0 - lng2 * Math.PI / 180.0;
		var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(La3 / 2), 2) + Math.cos(La1) * Math.cos(La2) * Math.pow(Math.sin(Lb3 / 2), 2)));
		s = s * 6378.137;//
		s = (Math.round(s * 10000) / 10000).toFixed(2);

        return s;
    }


module.exports = {
	toDate: toDate,
	toDateNoTime: toDateNoTime,
	isAuthorizationTrue: isAuthorizationTrue,
	isInteger: isInteger,
	getTouchData: getTouchData,
	formatTel: formatTel,
	formatTime: formatTime,
	formatData: formatData,
	formatUserName: formatUserName,
	getLabel: getLabel,
	arraySort: arraySort,
	isLogin: isLogin,
	getTimestamp: getTimestamp,
	isHaveStr,
	getDistance,
	formatBankNo,
	numberZzs,
	isPhone,
	getNowFormatDate,
	phoneNumberSafe,
	userIdCardSafe
}
